import { http } from './http_service'

export function getDependencies() {
  return http().get('/dependencies')
}

export function getDependenciesActive(active) {
  return http().get(`/dependencies/${active}`)
}

export function addDependence(data) {
  return http().post('/dependence', data)
}

export function activateDesactivate(id) {
  return http().post(`/dependence/${id}`)
}

export function getDependenceById(id) {
  return http().get(`/dependence/${id}`)
}

export function updateDependence(id, data) {
  return http().put(`/dependence/${id}`, data)
}
