<template>
<!--  eslint-disable -->
  <div>
    <validation-observer 
      ref="dependence"
      #default="{invalid}">
      <b-modal
        id="editDependence"
        ref="editModal"
        hide-footer
        title="Editar Dependencia"
        scrollable
      >
      <ErrorValidation v-if="validationErrors" :errors="validationErrors" />
        <div class="d-block">
          <form @submit.prevent="updateDependence">
            <div class="form-row">

              <!-- Dependencia -->
              <div class="form-group col-md-12">
                <label for="edit_dependence">Dependencia</label>
                <validation-provider
                  #default="{ errors }"
                  name="dependencia"
                  rules="required|alpha_accents"
                  >
                  <b-form-input
                    id="edit_dependence"
                    v-model="editDependence.description"
                    type="text"
                    class="form-control"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </div>

            </div>

            <hr>

            <div class="text-right">
              <button
                type="button"
                class="btn btn-danger btn-sm mx-1"
                @click="hideModal()"
              >
                Cancelar
              </button>
              <button
                type="submit"
                class="btn btn-primary btn-sm"
                :disabled="invalid"
              >
                <span class="fa fa-check" /> Actualizar
              </button>
            </div>
          </form>
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/*eslint-disable*/

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Servicios
import * as dependenceService from '@/services/dependence'

export default {

  props: {
    editDependence: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      validationErrors: '',
    }
  },

  methods: {

    async updateDependence() {
      try {
        const response = await dependenceService.updateDependence(this.editDependence.id, this.editDependence)

        if (response.status === 200) {
          this.$emit('reload')
          this.hideModal()
          this.showToast('Actualizado', 'CheckIcon', 'Dependencia Actualizada', 'success')
        }
      } catch (error) {
          if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
          } else {
            this.showToast('Advertencia', 'AlertCircleIcon', 'La dependencia no fue actualizada', 'warning')
          }        
      }
    },

    hideModal() {
      this.$refs.dependence.reset()
      this.validationErrors = ''
      this.$refs.editModal.hide()
      this.$emit('reload')
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}

</script>
