<template>
  <!--  eslint-disable -->
  <div>
    <validation-observer 
      ref="dependence"
      #default="{invalid}">
      <!-- Modal para Agregar Dependencia -->
      <b-modal
        id="modal-1"
        ref="dependenceModal"
        hide-footer
        title="Agregar Dependencia"
        scrollable
        @hidden="hideDependenceModal"
      >
      <ErrorValidation v-if="validationErrors" :errors="validationErrors" />
        <div class="d-block">
          <form @submit.prevent="addDependence">
            <div class="form-row">

              <!-- Descripción -->
              <div class="form-group col-md-12">
                <label for="description">Descripción</label>
                <validation-provider
                    #default="{ errors }"
                    name="descripción"
                    rules="required|alpha_accents"
                  >
                  <b-form-input
                    id="description"
                    v-model="dependence.description"
                    type="text"
                    class="form-control"
                    placeholder="Nombre de la dependencia"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </div>

            </div>

            <hr>

            <div class="text-right">
              <button
                type="button"
                class="btn btn-danger btn-sm mx-1"
                @click="hideDependenceModal"
              >
                Cancelar
              </button>
              <button
                type="submit"
                class="btn btn-primary btn-sm"
                :disabled="invalid"
              >
                <span class="fa fa-check" /> Guardar
              </button>
            </div>
          </form>
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/* eslint-disable */

// Servicios
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import * as dependenceService from '@/services/dependence'

export default {

  name: 'AddDependence',

  components: {
    ToastificationContent,
  },

  data() {
    return {
      validationErrors: '',
      dependence: {
        description: '',
      },
    }
  },

  methods: {

    async addDependence() {
      try {
        const response = await dependenceService.addDependence(this.dependence)

        if (response.status === 200) {
          this.$emit('reload')
          this.hideDependenceModal()
          this.showToast('Agregado', 'CheckIcon', 'Dependencia Agregada', 'success')
        }
      } catch (error) {
         if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
         } else {
            this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al agregar la dependencia', 'warning')
         }
      }
    },

    hideDependenceModal() {
      this.$refs.dependence.reset()
      this.$refs.dependenceModal.hide()
      this.cleanForm()
    },

    cleanForm() {
      this.validationErrors = ''
      this.dependence = { description: '' }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },
}

</script>

