<template>
  <div>
    <b-card>

      <!-- Título -->
      <h2 class="title center-mobile">
        Dependencias
      </h2>

      <b-row>
        <!-- Boton Agregar -->
        <b-col
          sm="6"
          class="d-flex justify-content-start mb-2"
        >
          <div class="center-mobile">
            <b-button
              v-b-modal.modal-1
              variant="primary"
              @click="showModal = true"
            >
              <span class="text-nowrap">Agregar</span>
            </b-button>
          </div>
        </b-col>

        <!-- Input Buscar -->
        <b-col
          sm="6"
          class="custom-search d-flex justify-content-end mb-2"
        >
          <div class="center-mobile">
            <b-form-group class="">
              <div class="d-flex align-items-center">
                <label class="mr-1">Buscar</label>
                <b-form-input
                  v-model="searchTerm"
                  placeholder="Buscar"
                  type="text"
                  class="d-inline-block"
                />
              </div>
            </b-form-group>
          </div>
        </b-col>
      </b-row>

      <!-- Tabla -->
      <vue-good-table
        v-if="!loading"
        class="animated fadeIn"
        :columns="columns"
        :rows="rows"
        :rtl="direction"
        :search-options="{
          enabled: true,
          externalQuery: searchTerm }"

        :pagination-options="{
          enabled: true,
          perPage:pageLength
        }"
      >
        <template
          slot="table-row"
          slot-scope="props"
        >

          <!-- Columna: Status -->
          <span v-if="props.column.field === 'status'">
            <b-form-checkbox
              :checked="[props.row.status === 1 ? 'true' : 'false']"
              :class="[props.row.status === 1 ? 'custom-control-success' : 'custom-control-secondary']"
              name="check-button"
              switch
              @change="changeStatus(props.row.id)"
            />
          </span>

          <!-- Columna: Opciones -->
          <span v-else-if="props.column.field === 'action'">
            <span>
              <b-dropdown
                variant="link"
                toggle-class="text-decoration-none"
                no-caret
              >
                <template v-slot:button-content>
                  <feather-icon
                    icon="MoreVerticalIcon"
                    size="16"
                    class="text-body align-middle mr-25"
                  />
                </template>

                <!-- Boton Editar  -->
                <b-dropdown-item
                  v-b-modal.editDependence
                  @click="editDependence(props.row)"
                >
                  <feather-icon
                    icon="Edit2Icon"
                    class="mr-50"
                  />
                  <span>Editar</span>
                </b-dropdown-item>
              </b-dropdown>
            </span>
          </span>

          <!-- Columna: Common -->
          <span v-else>
            {{ props.formattedRow[props.column.field] }}
          </span>
        </template>

        <!-- Paginación -->
        <template
          slot="pagination-bottom"
          slot-scope="props"
        >
          <div class="d-flex justify-content-between flex-wrap">
            <div class="d-flex align-items-center mb-0 mt-1">
              <span class="text-nowrap ">
                Mostrando 1 de
              </span>
              <b-form-select
                v-model="pageLength"
                :options="['3','5','10']"
                class="mx-1"
                @input="(value)=>props.perPageChanged({currentPerPage:value})"
              />
              <span class="text-nowrap">de {{ props.total }} datos </span>
            </div>
            <div>
              <b-pagination
                :value="1"
                :total-rows="props.total"
                :per-page="pageLength"
                first-number
                last-number
                align="right"
                prev-class="prev-item"
                next-class="next-item"
                class="mt-1 mb-0"
                @input="(value)=>props.pageChanged({currentPage:value})"
              >
                <template #prev-text>
                  <feather-icon
                    icon="ChevronLeftIcon"
                    size="18"
                  />
                </template>
                <template #next-text>
                  <feather-icon
                    icon="ChevronRightIcon"
                    size="18"
                  />
                </template>
              </b-pagination>
            </div>
          </div>
        </template>

        <div slot="emptystate">
          No hay datos para mostrar :-(
        </div>

      </vue-good-table>

      <loading :loading="loading" />

    </b-card>

    <!-- Modal Para Agregar Dependencia -->
    <DependenceAdd
      ref="dependenceModal"
      @reload="getDependencies"
    />

    <!-- Modal Para Editar Dependencia -->
    <DependenceEdit
      ref="editModal"
      :edit-dependence="this.dataDependence"
      @reload="getDependencies"
    />

  </div>
</template>

<script>

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Store
import store from '@/store/index'

// Servicios
import * as dependenceService from '@/services/dependence'

// Components
import DependenceAdd from '@/views/admin/dependence/dependence-add/DependenceAdd.vue'
import DependenceEdit from '@/views/admin/dependence/dependence-edit/DependenceEdit.vue'

export default {
  components: {
    DependenceAdd,
    DependenceEdit,
    ToastificationContent,
  },
  data() {
    return {
      loading: true,
      pageLength: 5,
      dir: false,
      columns: [
        {
          label: 'Descripción',
          field: 'description',
        },
        {
          label: 'Estado',
          field: 'status',
        },
        {
          label: 'Opciones',
          field: 'action',
        },
      ],
      rows: [],
      searchTerm: '',
      dataDependence: {},
    }
  },
  computed: {

    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true
        return this.dir
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false
      return this.dir
    },

  },

  created() {
    this.getDependencies()
  },

  methods: {

    async getDependencies() {
      try {
        const response = await dependenceService.getDependencies()

        if (response.status === 200) {
          this.loading = false
          this.rows = response.data.data
        }
      } catch (error) {
        this.loading = false
      }
    },

    async changeStatus(id) {
      try {
        const response = await dependenceService.activateDesactivate(id)

        if (response.status === 200) {
          this.getDependencies()
          this.showToast('Actualizado', 'CheckIcon', 'Estado actualizado correctamente', 'success')
        }
      } catch (error) {
        this.showToast('Alerta!', 'AlertCircleIcon', 'Error al actualizar el estado', 'warning')
      }
    },

    editDependence(data) {
      this.dataDependence = data
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}
</script>

<style lang="css" >

    .title {
        font-size: 32px;
        margin-top: 5px;
        margin-bottom: 50px;
    }

    .status_active {
        background-color: #1e8a54
    }

    .status_inactive {
        background-color: #7a7f7f
    }

    /* Centrar Contenido*/
    @media (max-width:575px){
        .center-mobile{
            display:table;
            margin:0 auto;
        }

        .title {
            margin-bottom: 20px;
        }
    }

</style>
